// =====================================================================
// Paywall
// =====================================================================
@import 'variables';
// @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@400;500;700&display=swap');

.paywall-container {
  width: 100%;
  position: relative;
  text-align: center;

  &.variant-a {
    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 230px;
      margin-top: -230px;
      background: linear-gradient(0deg, #fff 12.3%, rgba(255, 255, 255, 0) 89.6%);
    }

    h4 {
      font-weight: 400;
      line-height: normal;
      padding-bottom: 22px;
    }
  }

  h4 {
    font-size: 1rem;
    position: relative;
    overflow: hidden;
    padding-bottom: 1rem;
    margin-bottom: 0;

    &::before {
      position: absolute;
      top: 30%;
      margin-left: -50%;
      content: '\a0';
      width: 48%;
      background-color: $color-grey-light;
      height: 1px;
      @media (min-width: 768px) {
        width: 49%;
      }
    }

    &::after {
      position: absolute;
      top: 30%;
      margin-left: 2%;
      content: '\a0';
      width: 49%;
      background-color: $color-grey-light;
      height: 1px;

      @media (min-width: 768px) {
        margin-left: 1%;
      }
    }
  }

  a {
    color: #a50034;
    border-bottom: 0.5px solid $color-brand;
  }

  .subscriber-query-container {

  }

  .paywall-content-container {
    &.variant-a {
      padding: 25px 15px 16px;
      background-color: $ads-color;

      p {
        font-size: 12px;
        color: $color-grey-light;
        margin-bottom: 0;
      }

      .paywall-header {
        h1 {
          margin-bottom: 25px;
        }
      }
    }

    padding: 30px 15px 0;
    background-color: $ads-color;

    @media (min-width: 768px) {
      padding: 30px 30px 0;
    }

    p {
      margin-top: 0;
      margin-bottom: 15px;
    }

    h1 {
      margin: 0;
    }

    .paywall-header {
      text-align: center;

      p {
        font-size: 1.6rem;
        margin-bottom: 10px;
        line-height: 137%;
      }

      h1 {
        font-size: 2.4rem;
        line-height: 130%;
        margin-bottom: 30px;
        color: $color-black;
      }

      .header-highlight {
        font-size: 38px;
      }

      h4 {
        font-size: 1.8rem;
      }
    }

    .paywall-plan {
      background-color: $color-white;
      padding: 25px 25px 20px;
      margin: 0 0 20px;
      border: 1px solid $color-whitepale;
      border-radius: 5px;

      &.recommended-plan {
        border: 1px solid $color-pink;
        box-shadow: 0 0 10px rgba(103, 103, 103, 0.25);
        position: relative;

        .recommended {
          position: absolute;
          top: -10px;
          background-color: $color-pink;
          border-radius: 10px;
          padding: 4px 12px;

          .description {
            font-size: 1.4rem;
            color: $color-white;
            margin-bottom: 0;
            line-height: 100%;
          }
        }
      }

      .paywall-content {
        &.variant-a {
          display: block;

          .paywall-plan-description {
            text-align: center;

            h2 {
              font-size: 32px;
              letter-spacing: 1px;
              line-height: normal;

              &.shinmin-red {
                color: $color-red-shinmin;
              }

              .duration {
                font-size: 1.6rem;
                font-weight: 400;
              }

              .ori-price {
                color: $color-grey-light;
                font-size: 16px;
                padding-left: 5px;
                font-weight: 400;
              }

              @media (min-width: 768px) {
                font-size: 34px;
              }
            }

            h3 {
              font-size: 16px;
              font-weight: 400;
              margin-bottom: 0;
            }
          }

          .paywall-cta-container {
            padding-top: 20px;
            margin: 0 auto;

            .cta {
              &.cta-solid {
                &.cta-solid-red {
                  padding: 0 20px;

                  p {
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 32px;
                  }
                }
              }
            }
          }
        }

        display: block;

        @media (min-width: 768px) {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .paywall-plan-description {
          text-align: center;

          @media (min-width: 768px) {
            text-align: left;
          }

          h3 {
            font-size: 1.9rem;
            line-height: 150%;
            font-weight: 500;
            margin-bottom: 0.5rem;

            &.red {
              color: $color-brand;
            }

            .duration {
              font-size: 1.6rem;
              font-weight: 400;
            }
          }
        }

        .paywall-cta-container {
          text-align: center;

          p {
            &.caption {
              padding-top: 10px;
              font-size: 1.2rem;
              margin-bottom: 0;
            }
          }

          @media (min-width: 768px) {
            width: 23%;
          }

          @media (min-width: 992px) {
            width: 31%;
          }

          @media (min-width: 1280px) {
            width: 20%;
          }

          @media (min-width: 1680px) {
            width: 20%;
          }

          .cta {
            width: auto;
            border-radius: 100px;
            padding: 5px 20px;

            @media (min-width: 768px) {
              width: 100%;
            }

            &.cta-solid {
              &.cta-solid-red {
                letter-spacing: 1px;
                background-color: #a50034;
                color: #ffffff;
                padding: 5px 20px;
                height: auto;

                p {
                  color: $color-white;
                  margin: 0;
                  line-height: unset;
                }
              }
            }

            &.cta-bordered {
              height: auto;
              border-style: solid;
              border-width: 1px;
              font-size: 1.8rem;
              line-height: 28px;
              letter-spacing: 1px;

              p {
                color: $color-brand;
                margin: 0;
                line-height: unset;
              }
            }
          }
        }
      }

      &.variant-a {
        margin-bottom: 16px;
      }
    }

    .paywall-compare-chart {
      .paywall-compare-header {
        padding: 9px 0;
        text-align: center;
        border-top: 1px solid $color-whitepale;
        display: flex;
        justify-content: center;
        align-items: center;

        h4 {
          font-size: 1.4rem;
          font-weight: 400;
          color: $color-black;
          margin-bottom: 0;
        }

        img {
          width: 11px;
          height: auto;
          margin-left: 8px;
        }

        a {
          border: none;
        }

        .paywall-toggle {
          display: flex;
          // position: relative;
          margin-left: 5px;
          text-decoration: none;
        }

        .arrow {
          display: inline-block;
          width: 13px;
          margin-left: 5px;

          &.down {
            background-image: url('./../images/icon/arrow-down.svg');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
          }

          &.up {
            background-image: url('./../images/icon/arrow-down.svg');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            transform: rotate(-180deg);
          }
        }
      }

      .paywall-compare-content {
        border-top: 1px solid $color-whitepale;
        padding-bottom: 30px;
        padding-top: 1rem;

        .paywall-compare-table { //table
          border: none;
          width: 100%;
          margin: 0 auto;
          font-size: 1.6rem;
          line-height: 1.94;

          th {
            color: $color-black;
            background: none;
            border: none;
          }

          tr {
            background: none;
            border: none;

            th,
            td {
              &:nth-child(n + 2) {
                text-align: center;
              }

              border: none;
            }
          }

          @media (min-width: 768px) {
            width: 338px;
          }
        }
      }
    }
  }
}
// end paywall
